.utilisateurs-form-actions {
  margin-top: 0.5rem;
  padding-top: 0.5rem;
}
.utilisateurs-form-actions .flex {
  align-items: center;
}
.utilisateurs-form-actions ::ng-deep .p-button {
  min-height: 2.5rem;
  min-width: 7rem;
}

@media (max-width: 768px) {
  .utilisateurs-form-title {
    display: none;
  }
  .utilisateurs-form-sidebar {
    display: none;
  }
  .utilisateurs-form-card {
    box-shadow: none;
    border: none;
    border-radius: 0;
    padding: 0;
    background: transparent;
  }
  .utilisateurs-form-actions .flex {
    flex-direction: column;
    width: 100%;
  }
  .utilisateurs-form-actions ::ng-deep .p-button {
    width: 100%;
    justify-content: center;
    min-width: unset;
  }
}
